import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { Announcement } from 'app/core/models/announcement.model';
import { DateService } from 'app/core/services/date.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-announcement-content',
  templateUrl: './announcement-content.component.html',
  styleUrls: ['./announcement-content.component.scss']
})
export class AnnouncementContentComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Announcement>;
  displayedColumns: string[] = ['title', 'details', 'noOfView', 'postingDate', 'status'];
  statusList = [...STATUSES];

  announcement = new Announcement();
  announcementList = [];

  announcementForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dateService: DateService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.announcements).subscribe((res: any) => {
      this.announcementList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Announcement>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.announcement.postingDate = this.dateService.setDate(this.announcement.postingDate);
    this.apiService.save(ENDPOINTS.announcements, this.announcement).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  getAnnouncement(): void {
    this.apiService.findById(ENDPOINTS.announcements, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.announcement = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.announcement.postingDate = this.dateService.setDate(this.announcement.postingDate);
    this.apiService.update(ENDPOINTS.announcements, this.announcement).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.announcement.status = COMMON_FUNCTION.ACTIVE;
    this.announcementForm = this.formBuilder.group({
      title: ['', Validators.required],
      details: ['', Validators.required],
      noOfView: ['', Validators.required],
      postingDate: ['', Validators.required],
      status: [''],
      link: ['']
    });
  }

  resetFormValidator(): void {
    this.announcement = new Announcement();
    this.announcement.status = COMMON_FUNCTION.ACTIVE;
    this.announcementForm.markAsUntouched();
    this.announcementForm.markAsPristine();
  }

  disabledWeekEnds = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    return day !== 0 && day !== 6;
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this announcement?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.announcements, this.announcement.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Announcement successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
